import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    this.showEmail();
    this.showPhone();
  }

  scrollToElement(element): void {
    switch(element) {
      case 0:
        element = "page-header";
        break;
      case 1:
        element = "about";
        break;
      case 2:
        element = "services";
        break;
      case 3:
        element = "contact";
        break;
      default:
        element= "about";
    }

    var scrollElement = document.getElementById(element)
    scrollElement.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
  }

  activateLink(element) {
    switch(element) {
      case 1:
        element = "nav-about";
        break;
      case 2:
        element = "nav-services";
        break;
      case 3:
        element = "nav-contact";
        break;
      default:
        element= "nav-about";
    }

    var activateElement = document.getElementById(element)
    activateElement.classList.add('active');
  }

  deactivateLink(element) {
    switch(element) {
      case 1:
        element = "nav-about";
        break;
      case 2:
        element = "nav-services";
        break;
      case 3:
        element = "nav-contact";
        break;
      default:
        element= "nav-about";
    }

    var activateElement = document.getElementById(element)
    activateElement.classList.remove('active');
  }

  showPhone(){
      var element = document.getElementById('set-phone');
      element.innerHTML = "(+86) 133 7364 8721";    
  }

  showEmail(){
  var user = 'contact',
      domain = 'ShanghaiQuJu.com',
      element = document.getElementById('set-email');

      element.innerHTML = user + '@' + domain;    
  }
}
